*, :before, :after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

:root {
  --primary-green: #66944a9c;
  --primary-yellow: #d8ac48;
  --light-grey: #e9e9e9;
  --olive-black: #101706;
  --bg-yellow: #d8ac4859;
  --menu-font-size: 1.75rem;
  --title-font-size: 2.5rem;
  --title-font-size-smaller: 2.2rem;
  --medium-font-weight: 400;
  --shadow-item: #63636333 0px 2px 8px 0px;
}

body {
  color: var(--olive-black);
  -webkit-font-smoothing: antialiased;
  font-family: Libre Bodoni, serif;
  line-height: 1.5;
}

picture, video, canvas, svg {
  max-width: 100%;
  display: block;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

p {
  padding-bottom: 2rem;
  font-size: 1.2rem;
}

ol, ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
  list-style: none;
}

#root, #__next {
  isolation: isolate;
}

.header {
  background-color: var(--primary-green);
  flex-direction: column;
  height: 100vh;
  display: flex;
}

.header__group {
  justify-content: space-between;
  display: flex;
}

.header__btn__menu {
  background-color: #0000;
  border: none;
  height: 3.2rem;
  margin: auto 1rem auto 0;
  padding: 0;
}

.header__logo {
  max-width: 280px;
  padding: .9rem;
}

.header__nav {
  display: none;
}

.header__nav.active {
  text-align: center;
  font-size: var(--menu-font-size);
  font-weight: var(--medium-font-weight);
  justify-content: center;
  margin-top: 2.4rem;
  display: flex;
}

.nav__link {
  margin-bottom: 1.2em;
  font-size: 1.4rem;
}

.nav__btn {
  background-color: var(--primary-yellow);
  margin-bottom: 2.4rem;
  padding: .3rem 2.6rem;
  font-family: Libre Caslon Text, serif;
  font-size: 1rem;
}

.header__img__container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.header__img {
  height: 90vh;
  margin-left: -70vh;
  position: absolute;
  left: 50%;
}

.section {
  background-color: var(--bg-yellow);
  width: 100%;
}

.section__container {
  flex-direction: column;
  margin: 0 auto;
  padding: 2rem 1rem;
  display: flex;
}

.section--bg-grey {
  background-color: var(--light-grey);
}

.section--bg-green {
  background-color: var(--primary-green);
}

.section__title {
  font-size: var(--title-font-size);
  font-weight: var(--medium-font-weight);
  padding-bottom: 1.6rem;
}

.section__title--smaller {
  font-size: var(--title-font-size-smaller);
}

.about-us {
  background-image: url("olive-branch.99fe5275.svg");
}

.section__contact__email {
  font-size: 8vw;
  font-style: italic;
}

.section_contact_img {
  z-index: 0;
  height: 75vw;
  position: absolute;
  left: 20vw;
}

.section__contact__container {
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.section__contact__title {
  font-size: 6vw;
}

.section__img {
  box-shadow: var(--shadow-item);
  flex-shrink: 0;
  margin: 0 -1rem;
}

.section__img--no-padding {
  padding: 0;
}

.section__location__img {
  height: 70vh;
}

.img__about-us {
  margin-bottom: 2rem;
}

.footer__ul {
  flex-wrap: wrap;
  justify-content: space-around;
  column-gap: 32px;
  display: flex;
}

.nav__link--footer {
  font-size: 1.2rem;
}

.nav__btn--footer {
  width: 100%;
  margin: 2rem auto;
}

.footer__section__picture {
  place-self: center;
  height: 30vh;
}

.footer__section__picture__img {
  height: 100%;
}

.footer__address {
  text-align: center;
  margin-bottom: 2rem;
  padding: 2rem 0 0;
}

.section__subscribe__container {
  gap: 2rem;
}

.section__img--dining {
  margin-bottom: 2rem;
}

.header__nav .book-table__nav {
  flex-direction: row;
}

.booking-form {
  flex-direction: column;
  max-width: 500px;
  margin: auto;
  display: flex;
}

.input-container {
  margin: 1rem;
  display: grid;
}

.section__form {
  align-items: end;
  height: 100%;
  display: flex;
}

.header__form {
  padding: 1rem;
}

.form__olive-branch {
  z-index: -1;
  height: 100%;
  position: absolute;
  top: -1rem;
  left: -1rem;
}

.foot-note {
  text-align: center;
  margin: 0 auto;
  padding: 0;
  font-size: 1rem;
}

.chef-container {
  z-index: 0;
  background: url("knife.4bbf2798.webp") 10% 0 / contain no-repeat content-box border-box;
  position: relative;
  overflow: visible;
}

.p_with-background:after {
  content: "";
  z-index: 1;
  background: url("knife.4bbf2798.webp") no-repeat;
  position: absolute;
  top: 50%;
  left: 50%;
}

@media (width >= 600px) {
  .section__subscribe__container {
    flex-direction: row;
    align-items: center;
  }

  .subscribe__btn {
    text-align: center;
    min-width: 30%;
    margin: 0;
  }

  .section__img--dining {
    padding-bottom: 0;
  }

  .header__group {
    height: auto;
  }

  .section__container--footer {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 30vh;
    overflow: hidden;
  }

  .footer__section__picture {
    width: 20%;
  }

  .footer__section__picture__img {
    width: 100%;
  }

  .footer__section--details {
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    display: flex;
  }

  .footer__address {
    padding: 0;
  }

  .nav__btn--footer {
    margin: 0;
  }

  .footer__btn__link {
    justify-content: center;
    display: flex;
  }

  .section_contact_img {
    height: 70vh;
  }

  .footer__logo {
    display: flex;
  }

  .footer__section__picture {
    align-items: end;
    width: 30%;
    height: 100%;
    display: flex;
  }

  .footer__section__picture__img {
    align-items: end;
  }

  .footer__address {
    text-align: start;
    align-self: end;
    margin: 0 0 0 .4rem;
    font-size: 1rem;
  }

  .nav__btn--footer {
    align-self: center;
    max-width: 300px;
  }
}

@media (width >= 1000px) {
  .header__logo {
    max-width: 340px;
  }

  .section__container {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 4rem;
    padding: 6rem 4rem;
    font-size: 1.6rem;
  }

  .section__img {
    width: 45%;
    margin: 0;
  }

  .section__location__img {
    width: 60%;
  }

  .section_contact_img {
    height: 70vh;
    left: 30vw;
  }

  .section__contact__container {
    justify-content: center;
  }

  .section__subscribe__container {
    flex-direction: column;
  }

  .section_container_facilities {
    grid-template-columns: 1fr 1fr;
    display: grid;
  }

  .section__img--dining {
    width: 100%;
  }

  .header__img {
    object-fit: cover;
    height: 100vh;
    overflow: hidden;
    scale: 130%;
  }

  .header__nav {
    text-align: center;
    width: auto;
    height: 100%;
    font-size: var(--menu-font-size);
    grid-area: navi;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .header__img__container {
    grid-area: img;
  }

  .header {
    grid-template: "logo img"
                   "navi img"
                   / 2fr 7fr;
    display: grid;
    overflow: hidden;
  }

  .header__logo {
    justify-self: center;
    margin: 4rem auto;
  }

  .header__btn__menu {
    display: none;
  }

  .section__container {
    max-width: 1440px;
  }

  .section__container--footer {
    align-content: end;
    width: 100%;
    height: auto;
  }

  .footer__section--details {
    flex-direction: column;
    flex-grow: 1;
    justify-content: center;
    gap: 3rem;
    display: flex;
  }

  .section__contact__title {
    font-size: 2.5vw;
  }

  .section__contact__email {
    font-size: 5vw;
  }

  .form__olive-branch {
    z-index: -1;
    width: 100%;
    height: auto;
    position: absolute;
    top: 4rem;
    left: 0;
    scale: 120%;
  }

  .nav__link--footer {
    margin: 0;
    padding: 0;
  }
}
