*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

:root {
  --primary-green: rgba(102, 148, 74, 0.61);
  --primary-yellow: rgb(216, 172, 72);
  --light-grey: rgb(233, 233, 233);
  --olive-black: rgb(16, 23, 6);
  --bg-yellow: rgba(216, 172, 72, 0.35);
  --menu-font-size: 1.75rem;
  --title-font-size: 2.5rem;
  --title-font-size-smaller: 2.2rem;
  --medium-font-weight: 400;
  --shadow-item: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}

body {
  font-family: "Libre Bodoni", serif;
  color: var(--olive-black);
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}
/* img, */
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  font-size: 1.2rem;
  padding-bottom: 2rem;
}

ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

a {
  list-style: none;
  text-decoration: none;
  color: inherit;
}

#root,
#__next {
  isolation: isolate;
}
/* ----------------- */

.header {
  display: flex;
  flex-direction: column;
  background-color: var(--primary-green);
  height: 100vh;
}

.header__group {
  display: flex;
  justify-content: space-between;
}

.header__btn__menu {
  padding: 0;
  background-color: transparent;
  border: none;
  height: 3.2rem;
  margin: auto 1rem auto 0;
}

.header__logo {
  padding: 0.9rem;
  max-width: 280px;
}

.header__nav {
  display: none;
}

.header__nav.active {
  margin-top: 2.4rem;
  display: flex;
  justify-content: center;
  text-align: center;
  font-size: var(--menu-font-size);
  font-weight: var(--medium-font-weight);
}

.nav__link {
  font-size: 1.4rem;
  margin-bottom: 1.2em;
}

.nav__btn {
  font-family: "Libre Caslon Text", serif;
  font-size: 1rem;
  padding: 0.3rem 2.6rem;
  margin-bottom: 2.4rem;
  background-color: var(--primary-yellow);
}

.header__img__container {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
}

.header__img {
  height: 90vh;
  left: 50%;
  margin-left: -70vh;
  position: absolute;
}

.section {
  width: 100%;
  background-color: var(--bg-yellow);
}

.section__container {
  padding: 2rem 1rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

.section--bg-grey {
  background-color: var(--light-grey);
}

.section--bg-green {
  background-color: var(--primary-green);
}

.section__title {
  font-size: var(--title-font-size);
  font-weight: var(--medium-font-weight);
  padding-bottom: 1.6rem;
}

.section__title--smaller {
  font-size: var(--title-font-size-smaller);
}

.about-us {
  background-image: url(./assets/olive-branch.svg);
}

.section__contact__email {
  font-size: 8vw;
  font-style: italic;
}

.section_contact_img {
  height: 75vw;
  z-index: 0;
  position: absolute;
  left: 20vw;
}

.section__contact__container {
  height: 100vh;
  align-items: center;
  justify-content: center;
}

.section__contact__title {
  font-size: 6vw;
}

.section__img {
  flex-shrink: 0;
  margin: 0 -1rem;
  box-shadow: var(--shadow-item);
}

.section__img--no-padding {
  padding: 0;
}

.section__location__img {
  height: 70vh;
}

.img__about-us {
  margin-bottom: 2rem;
}

.footer__ul {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  column-gap: 32px;
}

.nav__link--footer {
  font-size: 1.2rem;
}

.nav__btn--footer {
  width: 100%;
  margin: 2rem auto;
}

.footer__section__picture {
  height: 30vh;
  place-self: center;
}

.footer__section__picture__img {
  height: 100%;
}

.footer__address {
  padding: 2rem 0 0 0;
  text-align: center;
  margin-bottom: 2rem;
}

.section__subscribe__container {
  gap: 2rem;
}

.section__img--dining {
  margin-bottom: 2rem;
}

.header__nav .book-table__nav {
  flex-direction: row;
}

.booking-form {
  max-width: 500px;
  margin: auto;
  display: flex;
  flex-direction: column;
}

.input-container {
  margin: 1rem;
  display: grid;
}

.section__form {
  display: flex;
  align-items: end;
  height: 100%;
}

.header__form {
  padding: 1rem;
}

.form__olive-branch {
  position: absolute;
  top: -1rem;
  left: -1rem;
  height: 100%;
  z-index: -1;
}

.foot-note {
  margin: 0 auto;
  padding: 0;
  font-size: 1rem;
  text-align: center;
}

.chef-container {
  position: relative;
  overflow: visible;
  z-index: 0;
  background: url("./assets/knife.webp") 10% 0% / contain no-repeat;
  background-origin: content-box;
}

.p_with-background::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%; /* positions image to the right edge so it overflows */
  /* transform: translateY(-50%); */

  background: url("./assets/knife.webp") no-repeat;
  z-index: 1;
}

@media (min-width: 600px) {
  .section__subscribe__container {
    flex-direction: row;
    align-items: center;
  }

  .subscribe__btn {
    margin: 0;
    text-align: center;
    min-width: 30%;
  }

  .section__img--dining {
    padding-bottom: 0;
  }

  .header__group {
    height: auto;
  }

  .section__container--footer {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    height: 30vh;
    overflow: hidden;
  }

  .footer__section__picture {
    width: 20%;
  }

  .footer__section__picture__img {
    width: 100%;
  }

  .footer__section--details {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
  }

  .footer__address {
    padding: 0;
  }

  .nav__btn--footer {
    margin: 0;
  }

  .footer__btn__link {
    display: flex;
    justify-content: center;
  }

  .section_contact_img {
    height: 70vh;
  }

  .footer__logo {
    display: flex;
  }

  .footer__section__picture {
    display: flex;
    height: 100%;
    width: 30%;
    align-items: end;
  }
  .footer__section__picture__img {
    align-items: end;
  }

  .footer__address {
    font-size: 1rem;
    margin: 0 0 0 0.4rem;
    text-align: start;
    align-self: end;
  }

  .nav__btn--footer {
    max-width: 300px;
    align-self: center;
  }
}

@media (min-width: 1000px) {
  .header__logo {
    max-width: 340px;
  }
  .section__container {
    font-size: 1.6rem;
    padding: 6rem 4rem;
    flex-direction: row;
    gap: 4rem;
    justify-content: space-between;
    align-items: center;
  }

  .section__img {
    width: 45%;
    margin: 0;
  }

  .section__location__img {
    width: 60%;
  }

  .section_contact_img {
    height: 70vh;
    left: 30vw;
  }

  .section__contact__container {
    justify-content: center;
  }

  .section__subscribe__container {
    flex-direction: column;
  }

  .section_container_facilities {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }

  .section__img--dining {
    width: 100%;
  }

  .header__img {
    height: 100vh;
    overflow: hidden;
    object-fit: cover;
    scale: 130%;
  }

  .header__nav {
    grid-area: navi;
    display: flex;
    width: auto;
    height: 100%;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: var(--menu-font-size);
  }

  .header__img__container {
    grid-area: img;
  }

  .header {
    display: grid;
    grid-template-columns: 2fr 7fr;
    grid-template-rows: auto;
    grid-template-areas:
      "logo img"
      "navi img";
    overflow: hidden;
  }

  .header__logo {
    margin: 4rem auto;
    justify-self: center;
  }

  .header__btn__menu {
    display: none;
  }

  .section__container {
    max-width: 1440px;
  }

  .section__container--footer {
    width: 100%;
    height: auto;
    align-content: end;
  }

  .footer__section--details {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    justify-content: center;
    gap: 3rem;
  }

  .section__contact__title {
    font-size: 2.5vw;
  }

  .section__contact__email {
    font-size: 5vw;
  }

  .form__olive-branch {
    scale: 120%;
    position: absolute;
    top: 4rem;
    left: 0;
    width: 100%;
    height: auto;
    z-index: -1;
  }

  .nav__link--footer {
    padding: 0;
    margin: 0;
  }
}
